.info {
  padding-top: 50px;
  padding-bottom: 110px;
}
.info__wrapper {
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 20px;

  display: flex;
}

.info__text {
  padding-left: 177px;
  padding-right: 20px;

  position: relative;
  color: #666666;

  &::before {
    position: absolute;
    content: "";

    left: -49px;
    top: 27px;

    background-image: url("../img/2005.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;

    width: 220px;
    height: 317px;
  }
}

.info__title {
  font-family: $font-primary;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
  color: $grey;

  margin-top: 23px;
  margin-bottom: 20px;
  letter-spacing: -1.8px;
}

.info__desc {
  font-family: $font-secondary;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  padding-bottom: 4px;

  &--dots {
    display: none;
  }
}

.info__btn {
  display: inline-block;
  vertical-align: top;

  transition: 0.3s;
  font-family: $font-primary;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  color: $blue;

  border: 1px solid $blue;
  border-radius: 5px;

  padding: 14px 46px;
  margin-top: 18px;

  &:hover {
    background-color: $blue;
    color: $white;
  }
}

@media(max-width: $desktop) {
  .info {
    padding-top: 0;
    padding-bottom: 83px;
  }

  .info__wrapper {
    width: 100%;
    padding: 0 19px;
    padding-right: 0;
  }

  .info__title {
    font-size: 32px;
    line-height: 45px;
    margin-bottom: 27px;
  }

  .info__text {
    padding-left: 188px;
    width: 100%;

    &::before {
      // left: -29px;
      // top: 27px;

      left: -55px;
      top: 25px;
      width: 263px;
      height: 359px;
    }
  }

  .info__desc {
    padding-left: 5px;
    width: 100%;
    margin-bottom: 2px;
    padding-right: 2px;

    &--dots {
      display: inline-block;
      vertical-align: top;
      width: 2px;
    }

    &--hidden {
      display: none;
    }
  }

  .info__btn {
    padding: 14px 44px;
    margin-top: 28px;
    margin-left: 7px;
  }
}

@media (max-width: $tablet) {
  .info {
    padding-bottom: 43px;
  }
  .info__text {
    padding: 0;

    &::before {
    display: none;
    }
  }

  .info__title {
    font-size: 28px;
    line-height: 39px;

    margin: 0;
    margin-top: 40px;
    margin-bottom: 21px;
    margin-left: -6px;
  }

  .info__desc {
    margin-left: -7px;
    margin-bottom: 10px;

    &:not(:last-child) {
      margin-bottom: 35px;
    }
  }

  .info__btn {
    margin-top: -50px;
    margin-left: 0;
  }
}
